import React, { useContext, useEffect, useState } from "react";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "./home.css";
import { Link } from "react-router-dom";
import { Context } from "../context/Context";
import { axiosInstance } from "../config";
// import axios from "axios";

export default function Home() {
  const { user } = useContext(Context);
  const [assignment, setAssignment] = useState([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const getAssignments = async () => {
      const res = await axiosInstance.get("/assignment/posts");
      setAssignment(res.data);
      setLoading(false);
    };
    getAssignments();
  }, []);
  return (
    <>
      <Container>
        {user ? <h6 className="greeting">Welcome to Admin page!</h6> : ""}
        {loading ? (
          <div>Menghitung Tugas...</div>
        ) : assignment.length === 0 ? (
          <h6>Tidak ada tugas untuk Kelas B</h6>
        ) : (
          <h6>
            Ada{" "}
            <u>
              <Link to={"assignments"}>
                <b>{assignment.length} Tugas</b>
              </Link>
            </u>{" "}
            untuk Kelas B
          </h6>
        )}

        <h1 className="text-center mt-5 mb-3">Select Menu.</h1>
        <Row className="justify-content-md-center justify-content-xs-center">
          <Col md={3} xs={12}>
            <Link to={"post"}>
              <div className="menu">Material</div>
            </Link>
          </Col>
          <Col md={3} xs={12}>
            <Link to={"schedules"}>
              <div className="menu">Schedules</div>
            </Link>
          </Col>
          <Col md={3} xs={12}>
            <Link to={"notes"}>
              <div className="menu">Notes</div>
            </Link>
          </Col>
        </Row>
        <Row className="justify-content-md-center justify-content-xs-center">
          <Col md={3} xs={12}>
            <Link to={"files"}>
              <div className="menu">Files</div>
            </Link>
          </Col>
          <Col md={3} xs={12}>
            <Link to={"assignments"}>
              <div className="menu">Assignments</div>
            </Link>
          </Col>
          <Col md={3} xs={12}>
            <Link to={"tools"}>
              <div className="menu">Tools</div>
            </Link>
          </Col>
        </Row>
      </Container>
    </>
  );
}
