import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Button from "react-bootstrap/Button";
import "./addassignment.css";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { useState } from "react";
// import axios from "axios";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Addassignment() {
  const [main, setMain] = useState("");
  const [date, setDate] = useState("");
  const [desc, setDesc] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    const newAssignment = {
      main,
      date,
      desc,
    };
    try {
      await axiosInstance.post("/assignment/post", newAssignment);
      Swal.fire("Good job!", "Tugas Berhasil Ditambahkan!", "success").then(() => {
        window.location.replace("/assignments");
      });
    } catch (err) {
      // Handle error
    }
  };

  // const handleSubmit = async (e) => {
  //   e.preventDefault();
  //   const newAssignment = {
  //     main,
  //     date,
  //     desc,
  //   };
  //   try {
  //     await axiosInstance.post("/assignment/post", newAssignment);
  //     Swal.fire("Good job!", "Tugas Berhasil Ditambahkan!", "success");
  //   } catch (err) {}
  // };
  return (
    <Container className="write">
      <Row>
        <Col md={10} xs={12}>
          <form className="writeform" onSubmit={handleSubmit}>
            <input type="text" className="writetask mx-4" placeholder="Mata Kuliah.." autoFocus={true} onChange={(e) => setMain(e.target.value)} />
            <input type="text" className="writetask mx-4" placeholder="day date month, time" onChange={(e) => setDate(e.target.value)} />
            <div className="writeformgroup">
              <ReactQuill placeholder="Write description here..." type="text" className="writetext" theme="snow" value={desc} onChange={setDesc}></ReactQuill>
            </div>
            <Button variant="success" className="mx-4" type="submit">
              Publish
            </Button>
          </form>
        </Col>
      </Row>
    </Container>
  );
}
