import React, { useContext, useEffect, useState } from "react";
import "./files.css";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import about from "../assets/about2.jpg";
import pdf from "../assets/pdf.png";
import doc from "../assets/doc.png";
import ppt from "../assets/ppt.png";
import Button from "react-bootstrap/Button";
import { Link, useLocation } from "react-router-dom";
// import axios from "axios";
import { Context } from "../context/Context";
import Selectcatfiles from "../components/Selectcatfiles";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Files() {
  const [files, setFiles] = useState([]);
  const { user } = useContext(Context);
  const [query, setQuery] = useState("");
  const [loading, setLoading] = useState(true);
  const [categoryEmpty, setCategoryEmpty] = useState(false); // State untuk menunjukkan apakah kategori kosong

  const location = useLocation();
  useEffect(() => {
    const getFiles = async () => {
      const category = new URLSearchParams(window.location.search).get("cat");
      const url = category ? `/file/posts?cat=${category}` : "/file/posts";
      try {
        const response = await axiosInstance.get(url);
        const sortedPosts = response.data.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        setFiles(sortedPosts);
        setLoading(false);
        setCategoryEmpty(sortedPosts.length === 0);
      } catch (error) {
        console.error(error);
        setLoading(false);
      }
    };

    getFiles();
  }, [location]);

  const handleDelete = async (id) => {
    try {
      const result = await Swal.fire({
        title: "Are you sure?",
        text: "File ini akan dihapus!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      });

      if (result.isConfirmed) {
        await axiosInstance.delete(`/file/post/${id}`);
        Swal.fire("Deleted!", "Your file has been deleted.", "success").then(() => {
          window.location.reload();
        });
      } else {
        Swal.fire("Cancelled", "You have cancelled deleting the task.", "info");
      }
    } catch (err) {
      // Handle error
    }
  };

  return (
    <Container>
      <h1 className="text-center mt-3 mb-4">Files.</h1>
      <Selectcatfiles />
      <Row className="mb-3">
        <Col md={6} xs={10}>
          <div className="search-box">
            <button className="btn-search">
              <i className="fas fa-search"></i>
            </button>
            <input type="text" className="input-search" placeholder="Search by title..." onChange={(e) => setQuery(e.target.value.toLowerCase())} />
            {/* e = adalah si inputan tersebut. Jadi e target adalah inputan yang ditarget valuenya */}
          </div>
        </Col>
      </Row>
      {user ? (
        <Row className="justify-content-end">
          <Col md={3} xs={6}>
            <Link to={"/addfile"}>
              <Button variant="success">Add New File</Button>
            </Link>
          </Col>
        </Row>
      ) : (
        ""
      )}
      {loading ? (
        <div className="text-center">Please wait for a moment...</div>
      ) : (
        <Row className="justify-content-center">
          {categoryEmpty ? (
            <div className="text-center mt-5">Tidak ada postingan untuk kategori ini.</div>
          ) : (
            files
              .filter((item) => item.title.toLowerCase().includes(query))
              .map((item) => {
                return (
                  <Col md={3} xs={10} key={item._id}>
                    {user ? <i className="fa-solid fa-trash-can trash-file" onClick={() => handleDelete(item._id)}></i> : ""}
                    <a href={item.drive} target="_blank">
                      <Card className="allcards">
                        <Card.Img variant="top" src={item.image} className="img-file" />
                        <Card.Body>
                          <Card.Title className="card-title">
                            <img src={item.format} alt="filetype" className="pic-type" /> {item.title}
                          </Card.Title>
                        </Card.Body>
                      </Card>
                    </a>
                  </Col>
                );
              })
          )}
        </Row>
      )}
    </Container>
  );
}
