import React, { useContext, useEffect, useState } from "react";
import "./notes.css";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";
import about from "../assets/about.jpg";
import Button from "react-bootstrap/esm/Button";
import { Link, useLocation } from "react-router-dom";
import Selectcatnotes from "../components/Selectcatnotes";
import axios from "axios";
import { Context } from "../context/Context";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Notes() {
  const location = useLocation();
  const { user } = useContext(Context);
  const [notes, setNotes] = useState([]);
  const [loading, setLoading] = useState(true); // State untuk menunjukkan status loading
  const [categoryEmpty, setCategoryEmpty] = useState(false); // State untuk menunjukkan apakah kategori kosong

  useEffect(() => {
    const getNotes = async () => {
      const category = new URLSearchParams(window.location.search).get("cat");
      const url = category ? `/note/posts?cat=${category}` : "/note/posts";
      try {
        const response = await axiosInstance.get(url);
        const sortedPosts = response.data.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        setNotes(sortedPosts);
        setLoading(false); // Set status loading menjadi false setelah data diterima
        setCategoryEmpty(sortedPosts.length === 0);
      } catch (error) {
        console.log(error);
        setLoading(false); // Set status loading menjadi false jika terjadi error
      }
    };

    getNotes();
  }, [location]);

  const handleDelete = async (id) => {
    try {
      const result = await Swal.fire({
        title: "Are you sure?",
        text: "Catatan ini akan dihapus!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      });

      if (result.isConfirmed) {
        await axiosInstance.delete(`/note/post/${id}`);
        Swal.fire("Deleted!", "Your note has been deleted.", "success").then(() => {
          window.location.reload();
        });
      } else {
        Swal.fire("Cancelled", "You have cancelled deleting the note.", "info");
      }
    } catch (err) {
      // Handle error
    }
  };
  // const handleDelete = async (id) => {
  //   try {
  //     {
  //       window.confirm("Klik OK untuk mengonfirmasi") ? await axiosInstance.delete(`/note/post/${id}`) : alert("Anda membatalkan menghapus notes");
  //       window.location.reload();
  //     }
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  return (
    <Container>
      <h1 className="text-center mt-3">Notes.</h1>
      <Selectcatnotes />
      {user ? (
        <Row className="justify-content-end mb-2">
          <Col md={3} xs={6}>
            <Link to={"/addnotes"}>
              <Button variant="success">Add New Notes</Button>
            </Link>
          </Col>
        </Row>
      ) : (
        ""
      )}
      {loading ? (
        <div className="text-center">Please wait for a moment...</div>
      ) : (
        <Row>
          {categoryEmpty ? (
            <div className="text-center mt-5">Tidak ada postingan untuk kategori ini.</div>
          ) : (
            notes.map((item) => {
              return (
                <Col md={4} xs={6} className="mt-3" key={item._id}>
                  {user ? <i className="fa-solid fa-trash-can" onClick={() => handleDelete(item._id)}></i> : ""}
                  <img src={item.image} alt="notespic" className="notes-pic" />
                  <button type="button" className="button-notes">
                    <a href={item.image} download={item.image.split("/").pop()} style={{ color: "white" }}>
                      Download
                    </a>
                  </button>
                </Col>
              );
            })
          )}
        </Row>
      )}
    </Container>
  );
}
