// import axios from "axios";
import React, { useContext, useRef } from "react";
import { Context } from "../context/Context";
import "./login.css";
import { axiosInstance } from "../config";

export default function Login() {
  const userRef = useRef();
  const passwordRef = useRef();
  const { dispatch, isFetching } = useContext(Context);

  const handleSubmit = async (e) => {
    e.preventDefault();
    dispatch({ type: "LOGIN_START" });
    try {
      const res = await axiosInstance.post("/auth/login", {
        username: userRef.current.value,
        password: passwordRef.current.value,
      });
      dispatch({ type: "LOGIN_SUCCESS", payload: res.data });
      window.location.replace("https://lib-collections.netlify.app/");
    } catch (err) {
      dispatch({ type: "LOGIN_FAILURE" });
      window.location.reload();
      alert("WRONG INPUT!");
    }
  };
  return (
    <div className="container-fluid h-custom mt-5">
      <div className="row d-flex justify-content-center align-items-center h-100">
        <div className="col-md-9 col-lg-6 col-xl-5">
          <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/draw2.webp" className="img-fluid" alt="loginpic" />
        </div>
        <div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
          <form onSubmit={handleSubmit}>
            <div className="divider d-flex align-items-center my-4">
              <p className="text-center fw-bold mb-0 admin-only">
                <u>Only Admin Can Login!</u>{" "}
              </p>
            </div>
            <div className="form-outline mb-4">
              <label className="form-label" for="form3Example3">
                Username
              </label>
              <input type="text" id="form3Example3" className="form-control form-control-lg" placeholder="Enter Username" ref={userRef} />
            </div>
            <div className="form-outline mb-3">
              <label className="form-label" for="form3Example4">
                Password
              </label>
              <input type="password" id="form3Example4" className="form-control form-control-lg" placeholder="Enter password" ref={passwordRef} />
            </div>
            <button type="submit" className="btn btn-primary btn-lg btn-login" disabled={isFetching}>
              Login
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}
