import React, { useEffect, useState } from "react";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";
import Form from "react-bootstrap/Form";
import "./addfile.css";
import about2 from "../assets/about2.jpg";
import Button from "react-bootstrap/esm/Button";
import pdf from "../assets/pdf.png";
import doc from "../assets/doc.png";
import ppt from "../assets/ppt.png";
import axios from "axios";
import upload from "../utils/upload";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Addfile() {
  const [cats, setCats] = useState([]);
  const [title, setTitle] = useState("");
  const [format, setFormat] = useState("");
  const [drive, setDrive] = useState("");
  const [category, setCategory] = useState("");
  const [image, setImage] = useState("");
  const [file, setFile] = useState(null);

  useEffect(() => {
    const fetchcats = async () => {
      const res = await axiosInstance.get("/cat/posts");
      setCats(res.data);
    };
    fetchcats();
  }, []);
  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = await upload(file);
    const newFile = {
      title,
      format,
      drive,
      category,
      image: url,
    };
    try {
      await axiosInstance.post("/file/post", newFile);
      Swal.fire("Good job!", "File Berhasil Ditambahkan!", "success").then(() => {
        window.location.replace("/files");
      });
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <Container>
      <form onSubmit={handleSubmit}>
        <Row className="justify-content-center">
          <Col md={5}>
            <div className="writeformgroup">
              <label htmlFor="fileInput">
                <i className="writeicon fa-solid fa-plus"></i>
              </label>
              <input type="file" id="fileInput" style={{ display: "none" }} onChange={(e) => setFile(e.target.files[0])} />
              {/* <input type="file" id="fileInput" onChange={(e) => setFile(e.target.files[0])} /> */}
            </div>
            {file && <img src={URL.createObjectURL(file)} alt="photoblog" className="file-image" />}
          </Col>
          <Col md={6}>
            <Row className="mb-4 mt-4">
              <Col md={3} xs={6}>
                <Form.Select aria-label="Default select example" className="option" onChange={(e) => setFormat(e.target.value)}>
                  <option value={""}>Pilih Tipe File</option>
                  <option value={pdf}>Pdf</option>
                  <option value={doc}>Doc</option>
                  <option value={ppt}>PPT</option>
                </Form.Select>
              </Col>
            </Row>
            <Row>
              <Col md={3} xs={6} className="mb-3">
                {
                  <Form.Select aria-label="Default select example" className="option" onChange={(e) => setCategory(e.target.value)}>
                    <option>Kategori / mata kuliah</option>
                    {cats.map((item) => {
                      return (
                        <option value={item.category} key={item._id}>
                          {item.category}
                        </option>
                      );
                    })}
                  </Form.Select>
                }
              </Col>
            </Row>
            <input type="text" className="title mb-2" placeholder="title..." onChange={(e) => setTitle(e.target.value)} />
            <input type="text" className="url mb=2" placeholder="Url drive..." onChange={(e) => setDrive(e.target.value)} />
            <br />
            {/* <input type="text" className="url mb-2" placeholder="image..." onChange={(e) => setImage(e.target.value)} /> */}
            <br />
            <Button variant="success" type="submit">
              Publish
            </Button>
          </Col>
        </Row>
      </form>
    </Container>
  );
}
