import React, { useState } from "react";
import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import "./schedules.css";

export default function Schedules() {
  const [kelasb, setKelasb] = useState(true);
  return (
    <Container>
      <h1 className="text-center mt-3 jadwal">Jadwal Informatika.</h1>
      <Row className="switch">
        <div className="classb" onClick={() => setKelasb(!kelasb)}>
          Switch Class
        </div>
      </Row>
      {kelasb ? (
        <Row className="jadwalB">
          <h5 className="mx-4 mt-3 mb-4">
            <u>
              <b>Jadwal Kelas B</b>
            </u>
          </h5>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Senin</b>
              <ul>
                <li>
                  7:30 - 08:50, <b>Pengantar Studi Islam</b> (303)
                </li>
                <li>
                  12:30 - 14:15, <b>Bahasa Indonesia</b> (404)
                </li>
                <li>
                  14:40 - 16:20, <b>Pengantar Studi Islam</b> (305)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Selasa</b>
              <ul>
                <li>
                  07:00 - 08:55, <b>Prinsip Dasar Sistem Komputasi</b> (403)
                </li>
                <li>
                  09:00 - 10:55, <b>Peradaban Islam</b> (401)
                </li>
                <li>
                  12:30 - 14:10, <b>Organisasi dan Arsitektur Komputer</b> (406)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Rabu</b>
              <ul>
                <li>
                  07:00 - 08:55, <b>Jaringan Komputer</b> (409)
                </li>
                <li>
                  12:30 - 14:10, <b>Islam dan Sains</b> (409)
                </li>
                <li>
                  14:20 - 16:20, <b>Prinsip Dasar Sistem Komputasi</b> (104)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Kamis</b>
              <ul>
                <li>
                  09:00 - 10:45, <b>Organisasi dan Arsitektur Komputer</b> (301)
                </li>
                <li>
                  12:40 - 14:20, <b>Islam dan Ilmu Sosial Humaniora</b> (405)
                </li>
              </ul>
            </div>
          </Col>
        </Row>
      ) : (
        <Row className="jadwalA ">
          <h5 className="mx-4 mt-3 mb-4">
            <u>
              <b>Jadwal Kelas A</b>
            </u>
          </h5>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Senin</b>
              <ul>
                <li>
                  07:00 - 08:55, <b>Prinsip Dasar sistem Komputasi</b> (301)
                </li>
                <li>
                  09:00 - 10:45, <b>Organisasi dan Arsitektur Komputer</b> (406)
                </li>
                <li>
                  10:50 - 12:30, <b>Pengantar Studi Islam</b> (305)
                </li>
                <li>
                  14:20 - 16:00, <b>Jaringan Komputer</b> (306)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Selasa</b>
              <ul>
                <li>
                  14:20 - 16:00, <b>Pengantar Studi Islam</b> (306)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Rabu</b>
              <ul>
                <li>
                  09:00 - 10:45, <b>Organisasi dan Arsitektur Komputer</b> (303)
                </li>
                <li>
                  10:50 - 12:20, <b>Islam dan Ilmu Sosial Humaniora</b> (303)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Kamis</b>
              <ul>
                <li>
                  07:00 - 08:55, <b>Peradaban Islam</b> (104)
                </li>
                <li>
                  10:50 - 12:20, <b>Islam dan Sains</b> (306)
                </li>
                <li>
                  15:30 - 17:10, <b>Prinsip Dasar Sistem Komputasi</b> (303)
                </li>
              </ul>
            </div>
          </Col>
          <Col md={6} xs={12}>
            <div className="senin">
              <b className="mx-4">Jum'at</b>
              <ul>
                <li>
                  09:35 - 11:15, <b>Bahasa Indonesia</b> (103)
                </li>
              </ul>
            </div>
          </Col>
        </Row>
      )}
    </Container>
  );
}
