import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import "./addpost.css";
import Button from "react-bootstrap/Button";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import about from "../assets/about.jpg";
// import Selectcataddblog from "../components/Selectcataddblog";
import { useEffect, useState } from "react";
import Form from "react-bootstrap/Form";
import DOMPurify from "dompurify";
// import axios from "axios";
import upload from "../utils/upload";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Addpost() {
  const [cats, setCats] = useState([]);
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [category, setCategory] = useState("");
  const [file, setFile] = useState(null);
  const [image, setImage] = useState("");

  useEffect(() => {
    const fetchcats = async () => {
      const res = await axiosInstance.get("/cat/posts");
      //   console.log(res.data);
      setCats(res.data);
    };
    fetchcats();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = await upload(file);
    const newPost = {
      title,
      text,
      category,
      image: url,
    };
    try {
      await axiosInstance.post("/blog/post", newPost);
      Swal.fire("Good job!", "Post Berhasil Ditambahkan!", "success").then(() => {
        window.location.replace("/post");
      });
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <Container className="write">
      <Row className="mb-3">
        <Col md={3} xs={6}>
          <Form.Select aria-label="Default select example" className="option" onChange={(e) => setCategory(e.target.value)}>
            <option>Kategori / mata kuliah</option>
            {cats.map((item) => {
              return (
                <option value={item.category} key={item._id}>
                  {item.category}
                </option>
              );
            })}
          </Form.Select>
        </Col>
      </Row>
      <Row>
        <Col md={12} sm={12}>
          {file && <img className="writephoto" src={URL.createObjectURL(file)} alt="photoblog" />}
          <form className="writeform" onSubmit={handleSubmit}>
            <div className="writeformgroup">
              <label htmlFor="fileInput">
                <i className="writeicon fa-solid fa-plus"></i>
              </label>
              <input type="file" id="fileInput" style={{ display: "none" }} onChange={(e) => setFile(e.target.files[0])} />
              <input type="text" placeholder="Title" className="writeinput" autoFocus={true} onChange={(e) => setTitle(e.target.value)} />
            </div>
            <div className="writeformgroup">
              <ReactQuill placeholder="Write something here..." type="text" className="writetext" value={text} onChange={setText}></ReactQuill>
            </div>
            <Button variant="success" type="submit">
              Publish
            </Button>
          </form>
        </Col>
      </Row>
    </Container>
  );
}
