import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import "./addnotes.css";
import Button from "react-bootstrap/Button";
import about from "../assets/about.jpg";
import { useEffect, useState } from "react";
import Form from "react-bootstrap/Form";
import axios from "axios";
import upload from "../utils/upload";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Addnotes() {
  const [cats, setCats] = useState([]);
  const [file, setFile] = useState(null);
  const [image, setImage] = useState("");
  const [category, setCategory] = useState("");
  useEffect(() => {
    const fetchcats = async () => {
      const res = await axiosInstance.get("/cat/posts");
      //   console.log(res.data);
      setCats(res.data);
    };
    fetchcats();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    const url = await upload(file);
    try {
      await axiosInstance.post("/note/post", {
        image: url,
        category,
      });
      Swal.fire("Good job!", "Catatan Berhasil Ditambahkan!", "success").then(() => {
        window.location.replace("/notes");
      });
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Container className="write">
      <Row>
        <Col md={10} xs={{ span: 10, offset: 1 }}>
          {file && <img className="writephoto" src={URL.createObjectURL(file)} alt="photoblog" />}
          <form onSubmit={handleSubmit}>
            <div className="writeformgroup">
              <label htmlFor="fileInput">
                <i className="writeicon fa-solid fa-plus"></i>
              </label>
              <input type="file" id="fileInput" style={{ display: "none" }} onChange={(e) => setFile(e.target.files[0])} />
            </div>
            <Col md={3} xs={6} className="mb-3">
              {
                <Form.Select aria-label="Default select example" className="option" onChange={(e) => setCategory(e.target.value)}>
                  <option>Kategori / mata kuliah</option>
                  {cats.map((item) => {
                    return (
                      <option value={item.category} key={item._id}>
                        {item.category}
                      </option>
                    );
                  })}
                </Form.Select>
              }
            </Col>
            <Button variant="success" type="submit">
              Publish
            </Button>
          </form>
        </Col>
      </Row>
    </Container>
  );
}
