import React, { useContext, useEffect, useState } from "react";
import "./singlepost.css";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { Link } from "react-router-dom";
import DOMPurify from "dompurify";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import Button from "react-bootstrap/esm/Button";
import { Context } from "../context/Context";
import { axiosInstance } from "../config";

export default function Singlepost() {
  const location = useLocation();
  const path = location.pathname.split("/")[2];
  const { user } = useContext(Context);
  const [post, setPost] = useState({});
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [updateMode, setUpdateMode] = useState(false);

  useEffect(() => {
    const getPost = async () => {
      const res = await axiosInstance.get("/blog/post/" + path);
      setPost(res.data);
      setTitle(res.data.title);
      setText(res.data.text);
    };
    getPost();
  }, [path]);
  const handleUpdate = async () => {
    try {
      await axiosInstance.put(`/blog/post/${post._id}`, { title, text });
      setUpdateMode(false);
      window.location.reload();
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <Container className="singlepost mt-3">
      <Row>
        <Col md={12} xs={12}>
          <img className="singlephoto" src={post.image} alt="singlephoto" />
          {updateMode ? <input className="writeinput mb-3 mt-2" value={title} onChange={(e) => setTitle(e.target.value)} /> : <h1 className="singleposttitle mt-3 mb-4">{post.title}</h1>}
          {user ? (
            <div className="edit">
              <i className="singleposticon fa-regular fa-pen-to-square" style={{ color: "green" }} onClick={() => setUpdateMode(true)}></i>
            </div>
          ) : (
            ""
          )}
          <div className="info">
            <span className="singlepostauthor">
              <b>{new Date(post.createdAt).toLocaleDateString("id-ID", { weekday: "long", year: "numeric", month: "long", day: "numeric" })}, </b>
              {post.category && (
                <Link to={`/post?cat=${post.category}`} className="link">
                  {post.category}
                </Link>
              )}
            </span>
          </div>
          {updateMode ? (
            <div className="writeformgroup">
              <ReactQuill placeholder="Tell your story..." type="text" className="writetext" value={text} onChange={setText}></ReactQuill>
            </div>
          ) : (
            <p
              className="desc"
              dangerouslySetInnerHTML={{
                __html: DOMPurify.sanitize(post.text),
              }}
            ></p>
          )}
          {updateMode && (
            <Button variant="success" type="submit" onClick={handleUpdate}>
              Update
            </Button>
          )}
        </Col>
      </Row>
    </Container>
  );
}
