import React, { useContext } from "react";
import "./topbar.css";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import logo from "../assets/logo.png";
import { Link } from "react-router-dom";
import { Context } from "../context/Context";

export default function Topbar() {
  const { user, dispatch } = useContext(Context);

  const handleLogout = () => {
    dispatch({ type: "LOGOUT" });
    window.location.replace();
  };
  return (
    <Navbar className="navbar" bg="#fffffe" expand="lg">
      <Container>
        <Navbar.Brand>
          <Link to={"/"}>
            <img src={logo} alt="logo" width={60} className="logo" />
            <span className="brandname">LIB-collections</span>
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav>
            <Nav.Link as={Link} to={"/"} className="home">
              Home
            </Nav.Link>
            <Nav.Link as={Link} to={"about"} className="about">
              About
            </Nav.Link>
            {user ? (
              <Nav.Link className="logout" onClick={handleLogout}>
                Logout
              </Nav.Link>
            ) : (
              <Nav.Link as={Link} to={"login"} className="admin">
                Admin
              </Nav.Link>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
