import React, { useState } from "react";
import Button from "react-bootstrap/esm/Button";
import Container from "react-bootstrap/esm/Container";
import { Link } from "react-router-dom";

function Randomize() {
  const [initialPeople, setInitialPeople] = useState("");
  const [finalPeople, setFinalPeople] = useState("");
  const [numGroups, setNumGroups] = useState("");
  const [groups, setGroups] = useState([]);

  function generateGroups() {
    const people = [...Array(parseInt(finalPeople) - parseInt(initialPeople) + 1).keys()].map((x) => parseInt(initialPeople) + x);
    let groups = [];

    // Randomize the order of the people
    for (let i = people.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [people[i], people[j]] = [people[j], people[i]];
    }

    // Divide the people into groups
    let startIndex = 0;
    const remainder = people.length % numGroups;
    for (let i = 0; i < numGroups; i++) {
      const groupSize = Math.floor(people.length / numGroups) + (i < remainder ? 1 : 0);
      groups.push(people.slice(startIndex, startIndex + groupSize));
      startIndex += groupSize;
    }

    setGroups(groups);
  }

  return (
    <Container>
      <h3 className="text-center mt-2 mb-3">
        <b>Random Group Generator</b>{" "}
      </h3>
      <label className="mb-3">
        <b>Nomor Awal:</b>
        <input type="number" value={initialPeople} onChange={(e) => setInitialPeople(e.target.value)} className="mx-2" />
      </label>
      <br />
      <label className="mb-3">
        <b>Nomor Akhir:</b>
        <input type="number" value={finalPeople} onChange={(e) => setFinalPeople(e.target.value)} className="mx-2" />
      </label>
      <br />
      <label>
        <b>Jumlah Kelompok:</b>
        <input type="number" value={numGroups} onChange={(e) => setNumGroups(e.target.value)} className="mx-2" />
      </label>
      <br />
      <Button className="btn-primary mt-2 mb-2" onClick={generateGroups}>
        Generate
      </Button>
      {groups.length > 0 ? (
        <>
          <ul>
            {groups.map((group, i) => (
              <li key={i}>
                Group {i + 1}: {group.join(", ")}
              </li>
            ))}
          </ul>
          <p style={{ fontSize: "15px" }}>
            <Link to={"/generatename"} target="_blank">
              kalian bisa masukkan hasil ke
              <b style={{ color: "dark-blue" }}>
                <u> Generate Names </u>
              </b>
            </Link>
            untuk mendapat NIM dan Nama sekaligus
          </p>
        </>
      ) : (
        <p>No results yet.</p>
      )}
    </Container>
  );
}

export default Randomize;
