import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";
import Form from "react-bootstrap/Form";
import { axiosInstance } from "../config";
// import axios from "axios";

export default function Selectcat() {
  const [cats, setCats] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchcats = async () => {
      const res = await axiosInstance.get("/cat/posts");
      setCats(res.data);
    };
    fetchcats();
  }, []);

  const handleCategoryChange = (event) => {
    const selectedCategory = event.target.value;
    const optionAwal = "Kategori / mata kuliah";
    if (selectedCategory == optionAwal) {
      navigate("/post");
    } else {
      navigate(`/post?cat=${selectedCategory}`);
    }
  };
  return (
    <Row className="justify-content-end mb-4">
      <Col md={3} xs={6}>
        <Form.Select aria-label="Default select example" className="option" onChange={handleCategoryChange}>
          <option>Kategori / mata kuliah</option>
          {cats.map((item) => {
            return <option key={item._id}>{item.category}</option>;
          })}
        </Form.Select>
      </Col>
    </Row>
  );
}
