import React, { useEffect } from "react";
import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Button from "react-bootstrap/Button";
import { useState } from "react";
// import axios from "axios";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function () {
  const [category, setCategory] = useState("");
  const [cats, setCats] = useState([]);

  useEffect(() => {
    const getcats = async () => {
      const res = await axiosInstance.get("/cat/posts");
      setCats(res.data);
    };
    getcats();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const newCategory = {
      category,
    };
    try {
      await axiosInstance.post("/cat/post", newCategory);
      Swal.fire("Good job!", "Category Berhasil Ditambahkan!", "success").then(() => {
        window.location.replace("https://lib-collections.netlify.app/");
      });
    } catch (err) {}
  };
  const handleDelete = async (id) => {
    try {
      const result = await Swal.fire({
        title: "Are you sure?",
        text: "Category ini akan dihapus!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      });

      if (result.isConfirmed) {
        await axiosInstance.delete(`/cat/post/${id}`);
        Swal.fire("Deleted!", "Your category has been deleted.", "success").then(() => {
          window.location.reload();
        });
      } else {
        Swal.fire("Cancelled", "You have cancelled deleting the category.", "info");
      }
    } catch (err) {
      // Handle error
    }
  };

  return (
    <Container className="write">
      <Row>
        <Col md={5} xs={12}>
          <form className="writeform" onSubmit={handleSubmit}>
            <input type="text" className="writetask mx-4" placeholder="Mata Kuliah.." autoFocus={true} onChange={(e) => setCategory(e.target.value)} />
            <Button variant="success" className="mx-4 mt-2 mb-3" type="submit">
              Publish
            </Button>
          </form>
        </Col>
        <Col md={5} xs={12} className="mx-4">
          <h5>Category</h5>
          {cats.map((item) => {
            return (
              <p key={item._id}>
                {item.category}
                <i className="singleposticon fa-regular fa-trash-can" onClick={() => handleDelete(item._id)}></i>
              </p>
            );
          })}
        </Col>
      </Row>
    </Container>
  );
}
