import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import "./post.css";
import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Button from "react-bootstrap/esm/Button";
import { Link } from "react-router-dom";
import Selectcat from "./Selectcat";
import { useLocation } from "react-router-dom";
import { useContext } from "react";
import { Context } from "../context/Context";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Post() {
  const location = useLocation();
  const { user } = useContext(Context);
  const [posts, setPosts] = useState([]);
  const [query, setQuery] = useState("");
  const [loading, setLoading] = useState(true);
  const [categoryEmpty, setCategoryEmpty] = useState(false); // State untuk menunjukkan apakah kategori kosong

  const getText = (html) => {
    const doc = new DOMParser().parseFromString(html, "text/html");
    return doc.body.textContent;
  };

  useEffect(() => {
    const getPosts = async () => {
      const category = new URLSearchParams(window.location.search).get("cat");
      const url = category ? `/blog/posts?cat=${category}` : "/blog/posts";
      try {
        const response = await axiosInstance.get(url);
        const sortedPosts = response.data.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        setPosts(sortedPosts);
        setLoading(false);
        setCategoryEmpty(sortedPosts.length === 0);
      } catch (error) {
        console.error(error);
        setLoading(false);
      }
    };

    getPosts();
  }, [location]);
  const handleDelete = async (id) => {
    try {
      const result = await Swal.fire({
        title: "Are you sure?",
        text: "Postingan ini akan dihapus!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      });

      if (result.isConfirmed) {
        await axiosInstance.delete(`/blog/post/${id}`);
        Swal.fire("Deleted!", "Your Post has been deleted.", "success").then(() => {
          window.location.reload();
        });
      } else {
        Swal.fire("Cancelled", "You have cancelled deleting the Post.", "info");
      }
    } catch (err) {
      // Handle error
    }
  };

  return (
    <Container className="posts">
      <h1 className="text-center mt-5 mb-3">Materi.</h1>
      {user ? (
        <Col md={3} xs={12} className="mx-4">
          <Link to={"/addcat"}>
            <Button variant="success" className="mb-2 mt-2">
              Add Category
            </Button>
          </Link>
        </Col>
      ) : (
        ""
      )}
      <Selectcat />
      <Row className="mb-3">
        <Col md={6} xs={{ span: 10, offset: 1 }}>
          <div className="search-box">
            <button className="btn-search">
              <i className="fas fa-search"></i>
            </button>
            <input type="text" className="input-search" placeholder="Search by title..." onChange={(e) => setQuery(e.target.value.toLowerCase())} />
          </div>
        </Col>
      </Row>
      {user ? (
        <Row className="justify-content-end mb-2">
          <Col md={3} xs={6}>
            <Link to={"/addpost"}>
              <Button variant="success">Add New Post</Button>
            </Link>
          </Col>
        </Row>
      ) : (
        ""
      )}

      {loading ? (
        <div className="text-center">Please wait for a moment...</div>
      ) : (
        <Row className="justify-content-md-center justify-content-xs-center">
          {categoryEmpty ? (
            <div className="text-center mt-5">Tidak ada postingan untuk kategori ini.</div>
          ) : (
            posts
              .filter((item) => item.title.toLowerCase().includes(query))
              .map((item) => {
                return (
                  <Col className="allposts" md={3} xs={{ span: 10, offset: 1 }} key={item._id}>
                    {user ? <i className="fa-solid fa-trash-can" onClick={() => handleDelete(item._id)}></i> : ""}
                    <img className="blogphoto" src={item.image} alt="blogphoto" />
                    <div className="postinfo">
                      <div className="postcats">
                        <span className="postcat">{item.category}</span>
                      </div>
                      <Link to={`/post/${item._id}`}>
                        <span className="posttitle">{item.title}</span>
                      </Link>
                      <hr />
                      <span className="postdate">{new Date(item.createdAt).toLocaleDateString("id-ID", { weekday: "long", year: "numeric", month: "long", day: "numeric" })}</span>
                    </div>
                    <p className="postdesc">{getText(item.text)}</p>
                  </Col>
                );
              })
          )}
        </Row>
      )}
    </Container>
  );
}
