import React from "react";
import "./about.css";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";
import about2 from "../assets/about2.jpg";

export default function About() {
  return (
    <>
      <h1 className="text-center mt-4 abouth1">About This Website.</h1>
      <Container>
        <Row className="justify-content-md-center justify-content-xs-center">
          <Col md={6} xs={12} className="justify-content-md-center justify-content-xs-center">
            <div className="gambar">
              <img src={about2} alt="material" className="about2" />
            </div>
          </Col>
          <Col md={5} xs={12} className="justify-content-md-center justify-content-xs-center">
            <div className="text">
              <br />
              <b>Hello world!</b> Let me explain about this simple website. this site's main function is to accommodate data such as materials, assignments, class schedules, documents, and also class notes to make it easier for students to
              access information in class. Hopefully this site can help you :)
            </div>
          </Col>
        </Row>
      </Container>
      <br />
      <br />
    </>
  );
}
