import React, { Component } from "react";
import DOMPurify from "dompurify";

export default class Model extends Component {
  render() {
    let modelStyle = {
      display: "block",
      backgroundColor: "rgba(0,0,0,0.8)",
    };
    return (
      <div className="modal" style={modelStyle} key={this.props._id}>
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Informasi Tugas {this.props.main}</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={this.props.hide}></button>
            </div>
            <div
              className="modal-body"
              dangerouslySetInnerHTML={{
                __html: DOMPurify.sanitize(this.props.desc),
              }}
            ></div>
          </div>
        </div>
      </div>
    );
  }
}
