import React, { useContext, useEffect, useState } from "react";
import Container from "react-bootstrap/esm/Container";
import "./assignments.css";
import Button from "react-bootstrap/esm/Button";
// import Modal from "react-bootstrap/Modal";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";
import { Link } from "react-router-dom";
// import axios from "axios";
import Model from "../components/Model";
import { Context } from "../context/Context";
import { axiosInstance } from "../config";
import Swal from "sweetalert2";

export default function Assignments() {
  const [assignment, setAssignment] = useState([]);
  const [model, setModel] = useState(false);
  const [tempdata, setTempdata] = useState([]);
  const [loading, setLoading] = useState(true);
  const { user } = useContext(Context);

  // const [lgShow, setLgShow] = useState(false);

  useEffect(() => {
    const fetchassignment = async () => {
      const res = await axiosInstance.get("/assignment/posts");
      setAssignment(res.data);
      setLoading(false);
    };
    fetchassignment();
  }, []);

  const getData = (main, date, desc) => {
    let tempData = [main, date, desc];
    setTempdata((item) => [1, ...tempData]);
    return setModel(true);
  };

  const handleDelete = async (id) => {
    try {
      const result = await Swal.fire({
        title: "Are you sure?",
        text: "Tugas ini akan dihapus!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      });

      if (result.isConfirmed) {
        await axiosInstance.delete(`/assignment/post/${id}`);
        Swal.fire("Deleted!", "Your Task has been deleted.", "success").then(() => {
          window.location.reload();
        });
      } else {
        Swal.fire("Cancelled", "You have cancelled deleting the task.", "info");
      }
    } catch (err) {
      // Handle error
    }
  };

  // const handleDelete = async (id) => {
  //   try {
  //     {
  //       window.confirm("Klik OK untuk mengonfirmasi") ? await axiosInstance.delete(`/assignment/post/${id}`) : alert("Anda membatalkan menghapus tugas");
  //       window.location.reload();
  //     }
  //   } catch (err) {}
  // };
  return (
    <>
      <h1 className="text-center mt-4 mb-5">Assignments.</h1>
      <Container>
        {user ? (
          <Row className="mb-2">
            <Col md={3} xs={12}>
              <Link to={"/addassignment"}>
                <Button variant="success">Add New Assignment</Button>
              </Link>
            </Col>
          </Row>
        ) : (
          ""
        )}
        {loading ? (
          <div className="text-center">Please wait for a moment...</div>
        ) : (
          assignment.map((item, index) => {
            return (
              <div className="taskwrapper" key={item._id}>
                <p>
                  <b>{index + 1}.</b> <b>{item.main}</b>, Deadline <b>{item.date}</b>
                  <button className="button-task mx-3" onClick={() => getData(item.main, item.date, item.desc)}>
                    more info
                  </button>
                  {user ? (
                    <span>
                      <i className="fa-solid fa-trash-can" onClick={() => handleDelete(item._id)}></i>
                    </span>
                  ) : (
                    ""
                  )}
                </p>
              </div>
            );
          })
        )}
      </Container>
      {model === true ? <Model main={tempdata[1]} date={tempdata[2]} desc={tempdata[3]} hide={() => setModel(false)} /> : ""}
    </>
  );
}
